<template>
  <div>
    <!-- <div style="height:100%;width:100%" id="container" width tabindex="0"></div> -->
    <div class="search-div" >
      <el-input id="tipInput" v-model="inputSearchVal" placeholder="">
      </el-input>
      <el-button class="button" slot="append" icon="el-icon-search" @click="searchKeyword">
        
      </el-button>
    </div>
    <div class="position" @click="posClick">
      <img src="~assets/img/select/position.svg" alt="">
      <span></span>
    </div>
  </div>
</template>

<script>
export default {
  name: "Search",
  data () {
    return {
      // map
      map: undefined,
      // 
      autoCompleteComponent: undefined,
      placeSearchComponent: undefined,
      // 
      // conheight: {
      //   height: '600px',
      //   width: ''
      // },
      // marker
      markerList: [],
      mapInput: '',
      inputSearchVal: '',
      result:'',
    }
  },
  mounted () {
    this.init();
    // this.addMarker();
    this.mapSearchInit();
  },
  created() {
    console.log('++++')
    this.inputSearchVal=''
  },
  methods: {
    /** map */
    init: function () {
      let map = new AMap.Map('container', {
        center: [116.397428, 39.90923],
        resizeEnable: true,
        zoom: 15,
        lang: 'cn'
      });
      AMap.plugin(['AMap.ToolBar', 'AMap.Scale'], function () {
        map.addControl(new AMap.ToolBar());
        map.addControl(new AMap.Scale());
      });
      this.map = map;
    },
    /**  */
    mapSearchInit() {
      let that = this;
      // 
      AMap.plugin(['AMap.Autocomplete', 'AMap.PlaceSearch'], function () {
        var autoOptions = {
          input: 'tipInput',
          city: '',
        };
        var autoCompleteComponent = new AMap.Autocomplete(autoOptions);
        // 
        AMap.event.addListener(autoCompleteComponent, 'select', function (data) {
          console.log(data);
          that.result = data.poi.name;
          that.$bus.$emit('mapSearchInit', that.result)
          that.$emit('mapSearchInit', that.result)
          if (data.poi.location != undefined) {
            //
            console.log('');
            that.map.setCenter(data.poi.location);
            //TODO marker

          }
        });
        that.autoCompleteComponent = autoCompleteComponent;
        // placeSearch
        that.placeSearchComponent = new AMap.PlaceSearch({
          // city citycodeadcode
          city: ''
        });
      });
    },
    /**  */
    searchKeyword() {
      this.placeSearchComponent.search(this.inputSearchVal, function (status, result) {
        // resultPOI
        // console.log(status);
      });
    },
    posClick() {
      let that=this;
      AMap.plugin('AMap.Geolocation', function () {
        var geolocation = new AMap.Geolocation({
          // true
          enableHighAccuracy: true,
          // 
          timeout: 10000,
          // Pixel(10, 20)
          buttonOffset: new AMap.Pixel(10, 20),
          //  false
          zoomToAccuracy: true,
          //  ,  RB
          buttonPosition: 'RB'
        })
        geolocation.getCurrentPosition()
        AMap.event.addListener(geolocation, 'complete', onComplete)
        AMap.event.addListener(geolocation, 'error', onError)

        function onComplete(data) {
          // data
          console.log(data)
          that.result=data.formattedAddress.substring(data.formattedAddress.length-8);
          console.log(that.result)
          that.$bus.$emit('posClick',that.result)
        }
        function onError(data) {
          // 
        }
      })
    }
  }
}
</script>

<style scoped>
.search-div {
  display: flex;
  width: 100%;
  /*align-items: center;*/
  /*width: 300px;*/
  /*height: 40px;*/
  /*box-sizing: border-box;*/
}
.position{
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background-color: #ffffff;
  border: 1px solid #eeeeee;
  margin-top: 10px;
  font-size: 16px;
}
.position img{
  width: 20px;
  height: 20px;
  position: relative;
  top:4px;
}
</style>