<template>
    <div class="input">
      <input
          placeholder=""
          @keyup.enter="search"
          v-model="commodityName"
          @input="input"
      ></input>
    </div>
</template>

<script>
export default {
  name: "InputBox",
  data(){
    return{
      commodityName:'',
    }
  },
  methods:{
    search(){
      this.$store.commit('getList',this.commodityName)
      this.$router.push('/result/'+this.commodityName)
    },
    input(){
      this.$store.commit('getName',this.commodityName)
    },
  }
}
</script>

<style scoped>
.input input{
  width: 100%;
  padding-left: 30px;
  background:url('../../../assets/img/common/search.svg') no-repeat 10px center/15px;
  text-align: initial;
  display: inline-block;
  height: 30px;
  line-height: 15px;
  border-radius: 10px;
  /*input*/
  /**/
  box-shadow:none;
  /*input*/
  outline: none;
  /*textarea */
  resize: none;
  /**/
  border: none;
  /*IOS*/
  -webkit-appearance: none;
  /**/
  -webkit-tap-highlight-color:rgba(0,0,0,0);
}
</style>