<template>
  <div>
    <div class="register-wrapper">
      <div id="register">
        <p class="title"></p>
        <el-form
            :model="ruleForm2"
            status-icon
            :rules="rules2"
            ref="ruleForm2"
            label-width="0"
            class="demo-ruleForm"
        >
          <el-form-item prop="username">
            <el-input v-model="ruleForm2.username" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="tel">
            <el-input v-model="ruleForm2.tel" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="smscode" class="code">
            <el-input v-model="ruleForm2.smscode" placeholder=""></el-input>
            <el-button type="primary" :disabled='isDisabled' @click="sendCode">{{buttonText}}</el-button>
          </el-form-item>
          <el-form-item prop="pass">
            <el-input type="password" v-model="ruleForm2.pass" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="checkPass">
            <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')" style="width:100%;"></el-button>
            <p class="login" @click="gotoLogin"></p>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    //
    let checkName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      }
      else {
        callback()
      }
    }
    // <!---->
    let checkTel = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (!this.checkMobile(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    //  <!---->
    let checkSmscode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    // <!---->
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""))
      } else {
        if (this.ruleForm2.checkPass !== "") {
          this.$refs.ruleForm2.validateField("checkPass");
        }
        callback()
      }
    }
    // <!---->
    let validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.ruleForm2.pass) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm2: {
        pass: "",
        checkPass: "",
        tel: "",
        smscode: "",
        username:"",
      },
      rules2: {
        pass: [{ validator: validatePass, trigger: 'change' }],
        checkPass: [{ validator: validatePass2, trigger: 'change' }],
        tel: [{ validator: checkTel, trigger: 'change' }],
        smscode: [{ validator: checkSmscode, trigger: 'change' }],
        username:[{ validator: checkName, trigger: 'change' }],
      },
      buttonText: '',
      isDisabled: false, // 
      flag: true
    }
  },
  methods: {
    // <!---->
    sendCode () {
      let tel = this.ruleForm2.tel
      if (this.checkMobile(tel)) {
        let arr=[]
        for(let i=0;i<4;i++){
          const num=Math.floor(Math.random()*9)
          arr.push(num);
        }
        const code=arr.join('')
        alert(":"+code+"");
        console.log(tel)
        let time = 60
        this.buttonText = ''
        this.isDisabled = true
        if (this.flag) {
          this.flag = false;
          let timer = setInterval(() => {
            time--;
            this.buttonText = time + ' '
            if (time === 0) {
              clearInterval(timer);
              this.buttonText = ''
              this.isDisabled = false
              this.flag = true;
            }
          }, 1000)
        }
      }
    },
    // <!---->
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          setTimeout(() => {
            alert('')
              this.$router.push({
                path:'/login',
                query:{
                  username:this.ruleForm2.username,
                  phone:this.ruleForm2.tel,
                  password:this.ruleForm2.pass,
                }
              })
          }, 400);
        } else {
          console.log("error submit!!");
          return false;
        }
      })
    },
    // <!---->
    gotoLogin() {
      this.$router.push({
        path: "/login"
      });
    },
    // 
    checkMobile(str) {
      let re = /^1\d{10}$/
      if (re.test(str)) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>

<style scoped>
