  background: url("../../../assets/img/common/bg1.jpeg");
  position: fixed;
  bottom: 0px;
  width: 100%;
  z-index: 999;
  height: 100%;
}
.loading-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: #aedff8;
  display: flex;
  align-items: center;
  justify-content: center;
}
.register-wrapper img {
  position: absolute;
  z-index: 1;
}
.register-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
#register {
  width: 340px;
  margin: 60px auto;
  /*background: #fff;*/
  padding: 20px 40px;
  /*border-radius: 10px;*/
  position: relative;
  z-index: 9;
}
.title {
  font-size: 26px;
  line-height: 50px;
  font-weight: bold;
  margin: 10px;
  text-align: center;
}
.el-form-item {
  text-align: center;
}
.login {
  margin-top: 10px;
  font-size: 14px;
