<template>
<div class="menu">
  <select-view
      label=""
      @click.native="openValue"
  >
  </select-view>
  <div class="menu-text">{{this.label}}</div>
  <div class="menu-text">
    <img src="~assets/img/common/redenvelope.svg" alt="">
    </div>
    <select-view
        label="">
    </select-view>
</div>
</template>

<script>
import SelectView from "../../../components/common/select/SelectView";
export default {
  name: "MenuView",
  components:{
    SelectView
  },
  data(){
    return{
      labelList:
          [
            {
              value:'',
              image1:require('../../../assets/img/select/position.svg'),
              image2:require('../../../assets/img/select/position_active.svg'),
            },
            {
              value: '',
              image1:require('../../../assets/img/select/sellCount.svg'),
              image2:require('../../../assets/img/select/sellCount_active.svg'),
            },
            {
              value: '',
              image1:require('../../../assets/img/select/score.svg'),
              image2:require('../../../assets/img/select/score_active.svg'),
            },
            {
              value: '',
              image1:require('../../../assets/img/select/price.svg'),
              image2:require('../../../assets/img/select/price_active.svg'),
            },
            {
              value: '',
              image1:require('../../../assets/img/select/eCar.svg'),
              image2:require('../../../assets/img/select/eCar_active.svg'),
            }
          ],
    }
  },
  props:{
    label:{
      type:String,
      default:''
    },
  },
  methods:{
    openValue() {
      this.isTop=!this.isTop;
      this.$emit('openValue',this.labelList)
    },
  }
}
</script>

<style scoped>
.menu{
  display: flex;
  font-size: 14px;
  border-bottom: 1px solid #eeeeee;
  overflow: hidden;
}
.menu-text{
  height: 40px;
  line-height: 40px;
  padding-left: 20px;
  /*margin: 0px 10px 0px 10px;*/
}
.menu-text img{
  width: 20px;
  height: 20px;
  position: relative;
  top:2px;
}
